<template>
  <div class="about">
    <v-row>
      <v-col cols="12" sm="6" class="mx-auto">
        <v-text-field v-model="firstName" label="First Name" outlined></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" sm="6" class="mx-auto">
        <v-text-field v-model="lastName" label="Last Name" outlined></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" sm="6" class="mx-auto">
        <v-select v-model="gender" :items="items" label="Gender" outlined></v-select>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" sm="6" class="mx-auto">
        <v-text-field v-model="salary" label="Salary" outlined></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" sm="6" class="mx-auto">
        <v-btn v-if="employeeId" color="info" @click="updateEmployee">Update</v-btn>
        <v-btn v-else color="info" @click="saveEmployee">Save</v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import EmployeeService from "../api/EmployeeService";
import { eventBus } from "../main";
export default {
  props: ["employeeId"],
  components: {},
  data() {
    return {
      firstName: null,
      lastName: null,
      gender: null,
      salary: null,
      items: ["Male", "Female"]
    };
  },

  methods: {
    async saveEmployee() {
      const employee = {
        firstName: this.firstName,
        lastName: this.lastName,
        gender: this.gender,
        salary: this.salary
      };

      const savedEmployee = await EmployeeService.addEmployee(employee);
      console.log(savedEmployee);
      this.resetForm();
      eventBus.$emit("employeeSaved", true);
      // this.$router.push("/employee/all");
    },
    resetForm() {
      this.firstName = null;
      this.lastName = null;
      this.gender = null;
      this.salary = null;
    },
    async updateEmployee() {
      const employee = {
        _id: this.employeeId,
        firstName: this.firstName,
        lastName: this.lastName,
        gender: this.gender,
        salary: this.salary
      };

      const response = await EmployeeService.updateEmployee(employee);
      const updingEmployee = response.data;
      // this.snackbar = true;
      eventBus.$emit("employeeSaved", true);
      setTimeout(() => {
        this.$router.push("/employee/all");
      }, 2000);
      console.log(updingEmployee);
    }
  },

  async created() {
    if (this.employeeId) {
      const response = await EmployeeService.getById(this.employeeId);
      const employee = response.data;
      this.firstName = employee.firstName;
      this.lastName = employee.lastName;
      this.gender = employee.gender;
      this.salary = employee.salary;
    }
  }
};
</script>

